
// 主题颜色
$theme-color: #20ABFE;

// 边框灰色
$border-gray-color: #ebedf0;




















@mixin flex-center($x: center, $y: center) {
  display: flex;
  justify-content: $x;
  align-items: $y;
}

@mixin lineClamp($line: 3) {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: $line;
  -webkit-box-orient: vertical;
}

@mixin ellipsis() {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.setting-base {

  .meta-desc {
    line-height: 1.7;
  }

  .poster {
    display: block;
    width: 350px;
    height: 305px;
    background: #f7f7f7;
    object-fit: cover;
  }
}